.categories {
  padding: 70px 0;
  &__title {
    margin-bottom: 26px;
  }
  &__content {
    @extend %grid;
  }
  &__card {
    position: relative;
    padding: 30px;
    padding-bottom: 29px;
    border-radius: 10px;
    min-height: 405px;
    background-color: $grey-light;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    grid-column: span 4;
    &--big {
      min-height: 511px;
      grid-column: span 6;
      .categories__image {
        top: 24%;
      }
    }
  }
  &__image {
    position: absolute;
    left: 0;
    top: 30%;
  }
  &__list {
    display: flex;
    margin-bottom: 17px;
    flex-wrap: wrap;
  }
  &__list-item {
    font-size: 0;
    &:not(:last-child) {
      margin-right: 30px;
    }
  }
  &__bottom {
    display: flex;
    align-items: center;
  }
  &__link {
    color: $primary;
    position: relative;
    transition: color .3s ease-in-out;
    font-weight: 700;
    font-size: 16px;
    margin-right: 5px;
    line-height: 120%;
    outline: none;
    &::after {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      height: 2px;
      width: 100%;
      background-color: $primary;
      transform: scale(0);
      transition: all .3s ease-in-out;
    }
    &:focus::after,
    &:hover::after {
      transform: scale(1);
    }
    &:focus + .circle--small {
      background-color: $primary-light;
      svg path {
        stroke: $white;
      }
    }
    &:hover + .circle--small{
      svg path {
        stroke: $white;
      }
      background-color: $primary;
    }
    &:active + .circle--small{
      svg path {
        stroke: $white;
      }
      background-color: $primary-shade;
      border-color: $primary-shade;
    }
    &:active {
      color: $primary-shade;
    }
    &:active::after {
      transform: scale(1);
      background-color: $primary-shade;
    }
  }
}
@media (max-width: 1024px) {
  .categories {
    &__card {
      grid-column: span 3;
      &:first-child {
        grid-column: span 6;
      }
      &:nth-child(2) {
        order: 1;
        .categories__image {
          top: 25%;
        }
      }
      &:last-child {
        order: 2;
      }
    }
    &__image {
      top: 25%;
    }
  }
}

@media (max-width: 768px) {
  .categories {
    &__card {
      min-height: 511px;
      &:nth-child(4) {
        order: 1;
      }
    }
  }
}
@media (max-width: 576px) {
  .categories {
    padding-bottom: 79px;
    &__content {
      grid-row-gap: 32px;
    }
    &__card {
      min-height: 206px;
      padding: 16px;
      &:first-child {
        min-height: 332px;
        padding: 30px;
        .card-title {
          font-size: 24px;
          line-height: 33px;
        }
        .categories__list {
          flex-direction: row;
          gap: 21px;
        }
        .card-title-circle {
          left: 6px;
          bottom: 8px;
        }
      }
      &:not(:first-child) {
        .categories__list-item {
          font-size: 10px;
          line-height: 100%;
        }
        .categories__list {
          margin-bottom: 10px;
        }
        .card-title {
          line-height: 16px;
        }
      }
      &:nth-child(2) {
        .card-title-circle {
          left: 6px;
        }
        .categories__image {
          top: unset;
        }
      }
      &:last-child {
        .card-title-circle {
          left: 6px;
        }
      }
      &--big .categories__image {
        top: unset;
      }
      &--big .categories__list-item:not(:last-child) {
        margin-right: 0;
      }
    }
    &__list {
      flex-direction: column;
      gap: 5px;
    }
    &__bottom {
      display: none;
    }
    &__image {
      top: unset;
      bottom: 16px;
    }
  }
  .card-title-circle {
    display: inline;
    bottom: 4px;
  }
}
