.card {
  min-height: 420px;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: start;
  gap: 24px;
  padding: 30px;
  grid-column: span 3;
  background-color: $grey-light;
  border-radius: 10px;
  &__image {
    position: absolute;
    z-index: 0;
    bottom: 55%;
    left: 50%;
    transform: translateX(-50%);
  }
  .card-title {
    position: relative;
    z-index: 2;
  }
}
.card-title-circle {
  display: none;
}
.buy-link {
  &::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    z-index: 0;
  }
}
